import React from 'react';
import './App.css';

import model from './kek.png';
import logo from './logo.png';

import Model from './components/Model/Model.js';
import Graph from './components/Graph/Graph.js';
import {Page} from './components/Page/Page.js';
import {Info} from './components/Info/Info.js';

import {TIME, STEP, E0} from './constants.js';

class App extends React.Component{
    constructor(){
        super();

        let startV = 100;

        this.state = {
            mode: 'path',
            C: 0.1,
            R: 3,
            V: startV,
            L: 0.1,
            I: 4,
            C_: 0.1,
            R_: 3,
            V_: startV,
            L_: 0.1,
            T: 1,
            T_: 1,
            E: 4,
            E_: 4
        };
    }

    _setData(C, R, V, L){
        this.setState({
            C,
            R,
            V,
            L
        });
    }

    _getR(){
        return this.state.R;
    }

    _getL(){
        return this.state.L*Math.pow(10, -3);
    }

    _getC(){
        return this.state.C*Math.pow(10, -6);
    }

    _getV(){
        return this.state.V;
    }


    _calcE(t){
        return this.state.E*Math.cos(this.state.V*t);
    }

    _calcB(){
        return this.state.R/(2*this.state.L*Math.pow(10, -3));
    }

    _calcVRes(){
        return (1/(2*Math.PI*Math.sqrt(this.state.L*this.state.C*Math.pow(10, -3)*Math.pow(10, -6)))).toFixed(0);
    }

    _calcVRres(){
        return 1/Math.sqrt(this._getL()*this._getC());
    }

    _calcVCres(){
        console.log(this._calcVRres()*Math.sqrt(1-2*Math.pow(this._calcB()/this._calcVRres(), 2)));
        return this._calcVRres()*Math.sqrt(1-2*Math.pow(this._calcB()/this._calcVRres(), 2));
    }

    _calcVLres(){
        return this._calcVRRes()/this._calcVCres();
    }

    _calcRFull(){
        return 1;//Math.sqrt(Math.pow(this.state.R, 2) + Math.pow(this.state.V*this.state.L*Math.pow(10, -3)-(1/(this.state.V*this.state.C*Math.pow(10, -6))), 2)).toFixed(2);
    }

    _calcFi(){
        return Math.atan((this.state.V*this.state.L*Math.pow(10, -3)-(1/(this.state.V*this.state.C*Math.pow(10, -6))))/this.state.R);
    }

    _calcI0(){
        return this.state.E/Math.sqrt(Math.pow(this.state.R, 2) + Math.pow(this.state.V*this.state.L*Math.pow(10, -3) - (1/(this.state.V*this.state.C*Math.pow(10, -6))), 2));     
    }

    _calcI(t){
        return this._calcI0()*Math.cos(this.state.V*t);
    }

    _calcUR(t){
        return this._calcI0()*this.state.R*Math.cos(this.state.V*t-this._calcFi());
    }

    _calcUL(t){
        //мм
        return this.state.V*(this.state.L*Math.pow(10, -3))*this._calcI0()*Math.cos(this.state.V*t+Math.PI/2-this._calcFi()); 
    }

    _calcUC(t){
        //нано
        return (this._calcI0()/(this.state.V*this.state.C*Math.pow(10, -6)))*Math.cos(this.state.V*t-Math.PI/2-this._calcFi());
    }

    _calcEdsData(){
        var arrX = [];
        var arrY = [];
        for(var i = 0; i < this.state.T; i += STEP){
            arrX.push(i.toFixed(2));
            arrY.push(this._calcE(i));
        }

        return {
            name: "ЭДС",
            color: "#000",
            x: arrX,
            y: arrY
        };
    }

    _calcURData(){
        var arrX = [];
        var arrY = [];

        for(var i = 0; i < this.state.T; i += STEP){
            arrX.push(i.toFixed(2));
            arrY.push(this._calcUR(i));
        }

        return {
            name: "U на резисторе",
            color: "#000",
            x: arrX,
            y: arrY
        };
    }

    _calcULData(){
        var arrX = [];
        var arrY = [];

        for(var i = 0; i < this.state.T; i += STEP){
            arrX.push(i.toFixed(2));
            arrY.push(this._calcUL(i));
        }

        return {
            name: "U на индукции",
            color: "#000",
            x: arrX,
            y: arrY
        };
    }

    _calcUCData(){
        var arrX = [];
        var arrY = [];

        for(var i = 0; i < this.state.T; i += STEP){
            arrX.push(i.toFixed(2));
            arrY.push(this._calcUC(i));
        }

        return {
            name: "U на конденсаторе",
            color: "#000",
            x: arrX,
            y: arrY
        };
    }

    _calcIData(){
        var arrX = [];
        var arrY = [];

        for(var i = 0; i < this.state.T; i += STEP){
            arrX.push(i.toFixed(2));
            arrY.push(this._calcI(i));
        }

        return {
            name: "I",
            color: "#000",
            x: arrX,
            y: arrY
        };
    }

    _onPathClick(){
        this.setState({
            mode: 'path'
        });
    }

    _onGraphClick(){
        this.setState({
            mode: 'graph'
        });
    }

    render(){
        var pageModelStyle = {
            marginLeft: this.state.mode === 'graph' ? '-73vw' : '10px'
        };

        var appStyle = {
            backgroundColor: this.state.mode === 'graph' ? '#000' : 'transparent'
        }

        var edsData = this._calcEdsData();
        var urData = this._calcURData();
        var ulData = this._calcULData();
        var ucData = this._calcUCData();
        var IData = this._calcIData();

        return(
            <div className="App" style={appStyle}>
                <h1>МОДЕЛИРОВАНИЕ 3.11</h1>
                <Page className="page-model" name="" style={pageModelStyle} onClick={this._onPathClick.bind(this)} active={this.state.mode === 'path'}>
                    <div className="model-input">
                        <img src={model} />
                        <div className="data_input">
                            <div className="data-title">Данные</div>
                            <div className="input">
                                <span>R</span>
                                <input type="text" value={this.state.R_} onChange={(e) => {
                                    this.setState({
                                        R_: e.target.value
                                    });
                                }}/>
                                <span>Ом</span>
                            </div>
                            <div className="input">
                                <span>L</span>
                                <input type="text" value={this.state.L_} onChange={(e) => {
                                    this.setState({
                                        L_: e.target.value
                                    });
                                }}/>
                                <span>мГн</span>
                            </div>
                            <div className="input">
                                <span>С</span>
                                <input type="text" value={this.state.C_} onChange={(e) => {
                                    this.setState({
                                        C_: e.target.value
                                    });
                                }}/>
                                <span>мкФ</span>
                            </div>
                            <div className="input">
                                <span>V</span>
                                <input type="text" value={this.state.V_} onChange={(e) => {
                                    this.setState({
                                        V_: e.target.value
                                    });
                                }}/>
                                <span>Гц</span>
                            </div>
                            <div className="input">
                                <span>t</span>
                                <input type="text" value={this.state.T_} onChange={(e) => {
                                    this.setState({
                                        T_: e.target.value
                                    });
                                }}/>
                                <span>С</span>
                            </div>
                            <div className="input">
                                <span>EDS</span>
                                <input type="text" value={this.state.E_} onChange={(e) => {
                                    this.setState({
                                        E_: e.target.value
                                    });
                                }}/>
                                <span>В</span>
                            </div>
                            <input type="submit" onClick={() => {
                                this.setState({
                                    R: parseFloat(this.state.R_),
                                    L: parseFloat(this.state.L_),
                                    C: parseFloat(this.state.C_),
                                    V: parseFloat(this.state.V_),
                                    T: parseFloat(this.state.T_),
                                    E: parseFloat(this.state.E_)
                                });
                            }} value={"Ввод"} />
                        </div>
                    </div>



                    <h2 style={{marginTop: 50}}>На конденсаторе</h2>
                    <Info
                        X={this._calcRFull()}
                        V={this._calcVRes()}

                        Ur={(Math.max.apply(null, urData.y)*1000).toFixed(2)}
                        Ul={(Math.max.apply(null, ulData.y)*1000).toFixed(2)}
                        Uc={(Math.max.apply(null, ucData.y)*1000).toFixed(2)}
                    />
                </Page>
                <Page className="page-graph" name="Графики" onClick={() => {}} active={this.state.mode === 'graph'} whiteMode={ this.state.mode === 'graph'}>
                    <Graph data={[edsData]} Xname={"t(с)"} Yname={"E(В)"}/>
                    {/* <Graph data={[urData]} />
                    <Graph data={[ulData]} /> */}
                    <Graph data={[ucData]} Xname={"t(с)"} Yname={"Uc(В)"}/>
                    <Graph data={[IData]} Xname={"t(с)"} Yname={"I(А)"} />
                </Page>
            </div>
        );
    }
}

export default App;
